@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.MasterDiv{position:relative;transition:all 0.3s ease}.MasterDiv #ButtonsDiv{margin:1rem auto;display:flex;align-items:center;justify-content:center;flex-wrap:wrap}.MasterDiv #ButtonsDiv button{padding:0.5rem 1rem;color:white;outline:none;border:none;border-radius:3px;margin:auto 0.1rem;cursor:pointer}.MasterDiv #ButtonsDiv button a{text-decoration:none;color:white}.MasterDiv #ButtonsDiv button:hover{opacity:0.8}.MasterDiv #ButtonsDiv button:disabled,.MasterDiv #ButtonsDiv button[disabled]{background-color:gray !important}.MasterDiv .container{max-width:1140px;margin:auto}@media (max-width: 1200px){.MasterDiv .container{max-width:960px}}@media (max-width: 992px){.MasterDiv .container{max-width:576px}}@media (max-width: 576){.MasterDiv .container{max-width:100%}}.MasterDiv>#input{display:flex;align-items:center;justify-content:space-between;padding:1rem}.MasterDiv>#input>div{display:flex;align-items:center;justify-content:center;color:white}.MasterDiv>#input>div>span{margin-right:1rem}.MasterDiv>#BarsDiv{height:85vh;position:relative;justify-content:space-between;border-bottom:2px solid black;display:flex;align-items:flex-start;margin:auto}@media (max-width: 768px){.MasterDiv>#BarsDiv{font-size:0.8rem !important}.MasterDiv>#BarsDiv>div{margin:0.15rem;padding:0.15rem}}.MasterDiv>#BarsDiv>#stat{margin:0.25rem;padding:0.25rem}.MasterDiv>#BarsDiv>#stat>div{width:10rem;display:flex;justify-content:space-between;align-items:center}.MasterDiv>#BarsDiv>#stat>div>span{display:inline-block;text-align:center;margin:0.1rem;margin-left:auto;padding:0.2rem;width:3rem;border:1px solid black}@media (max-width: 768px){.MasterDiv>#BarsDiv>#stat>div{width:8rem}.MasterDiv>#BarsDiv>#stat>div>span{padding:0.1rem;width:2.25rem}}.MasterDiv>#BarsDiv>#desc{display:inline-block;margin:0.25rem;padding:0.25rem}.MasterDiv>#BarsDiv>#desc>div{font-size:0.9rem}.MasterDiv>#BarsDiv>#desc>div>span{text-decoration:underline}@media (max-width: 768px){.MasterDiv>#BarsDiv>#desc>div{font-size:0.8rem}}.MasterDiv>#BarsDiv>#desc>.name{font-size:1.1rem;font-weight:bold;text-decoration:underline}.MasterDiv>#BarsDiv #switch{margin:1rem;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.MasterDiv>#BarsDiv #switch>div{border-radius:50px;width:2.8rem;height:1.1rem;background:white;border:2px solid red;margin-top:0.5rem}.MasterDiv>#BarsDiv #switch>div>div{width:0.9rem;height:0.9rem;border-radius:50%;margin:0.1rem;background-color:#1a2038;transition:all 0.3s ease}.MasterDiv>#BarsDiv #switch>div>.left{margin-right:auto}.MasterDiv>#BarsDiv #switch>div>.right{margin-left:auto}.MasterDiv>#BarsDiv>#barContainer{position:absolute;bottom:0;left:50%;transform:translateX(-50%);display:flex;align-items:flex-end}.MasterDiv>#BarsDiv>#barContainer>.bar{background:rgba(73,144,218,0.6);border:1px solid #547496}

.App{text-align:center;padding:0;margin:0}*{font-family:"Open Sans", sans-serif}.dark{background-color:#1a2038}.dark #input{background-color:#222945}.light{background-color:#fafafa}.light #input{background-color:#222945}

